/* INPUT BOXES */

.input {
  position: relative;
  z-index: 1;
  display: inline-block;
  width: 100%;
  vertical-align: top;
  margin-top: 10px;
}

.input__field {
  position: relative;
  display: block;
  padding: .8em 0;
  width: 60%;
  border: none;
  border-radius: 0;
  background: #f0f0f0;
  color: #222222;
  font-weight: 700;
  font-family: "OpenSans", sans-serif;
  -webkit-appearance: none;
}

.input__field:focus {
  outline: none;
}

.input__label {
  display: inline-block;
  float: right;
  /* padding: 0 1em; */
  width: 40%;
  color: #6a7989;
  font-weight: 400;
  font-size: 70.25%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.input__label-content {
  position: absolute;
  display: block;
  width: 100%;
  margin-top: 26px;
  font-size: 12pt;
  color: #222222;
  font-family: "OpenSans", sans-serif;
  font-weight: 400;
}

.input--hoshi {
  overflow: hidden;
}

.input__field--hoshi {
  margin-top: 8px;
  background: 0 0;
  color: #222222;
  font-size: 13.5pt;
  width: 93.75%;
  font-weight: 400;
  font-family: "OpenSans", sans-serif;
  padding: 19px 0 16px;
  text-decoration: underline;
}

.input__label--hoshi {
  position: absolute;
  bottom: 0;
  left: 0;
  /* padding: 0 .25em; */
  width: 100%;
  height: calc(99%);
  text-align: left;
  pointer-events: none;
}

.input__label-content--hoshi {
  position: absolute;
}

.input__label--hoshi::before,
.input__label--hoshi::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: calc(90%);
  border-bottom: 1px solid #222222;
}

.input__label--hoshi::after {
  margin-top: 0;
  border-bottom: 4px solid #0189ce;
  -webkit-transform: translate3d(-100%, 0, 0);
  transform: translate3d(-100%, 0, 0);
  -webkit-transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
}

.input__label--hoshi-color-2::after {
  border-color: #0189ce;
}

.input__field--hoshi:focus + .input__label--hoshi::after,
.input--filled .input__label--hoshi::after {
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.input--filled .input__label-content {
  color: #222222;
  font-size: 10.5pt;
  margin-top: -1px;
  font-weight: 600;
}

.second-field {
  margin-top: 54px;
}

button.btn.btn-form {
